.container_cart {
	padding: 0;
	margin-top: 3% !important;
}

.cart__pay {
	width: 100%;
	.header__cart {
		width: 100%;
		background-color: #f1f1f1;
		font-size: 1.5rem;
		font-weight: bold;
		padding: 10px 20px;
	}

	.btn-delete-back {
		display: flex;
		justify-content: space-between;
		width: 100%;
		font-size: 1.6rem;
		font-weight: bold;
		padding: 40px 0;
		button {
			width: 14rem;
			// color: #111;
			font-size: 1.3rem;
			height: auto;
			background-color: #f15e2c;
		}
	}
}

.details__cart {
	display: flex;
	width: 100%;
	justify-content: space-between;
	border-bottom: 1px solid #123;
	padding: 5% 0;
	.img_detail__cart {
		width: 80%;
		display: flex;
		justify-content: space-between;
		.logo {
			img {
				width: 14rem;
				height: auto;
			}
		}
		.detail {
			display: grid;
			justify-content: space-between;
			width: 100%;
			padding: 0 5%;

			.detail__content {
				// width: 100%;
				div {
					// font-weight: bold;
					// font-size: 1.4rem;
				}
				.name {
					font-weight: bold;
					font-size: 1.2rem;
				}
				.price {
					font-size: 1.2rem;
					margin-top: 4%;
					opacity: 0.7;
				}
			}

			.size-amount {
				display: flex;
				justify-content: space-between;
				margin-right: 2%;
				width: 100%;
				.size {
					// width: 45%;
					:last-child {
						// width: 45%;
					}
				}
				.amount {
					// width: 45%;
					:last-child {
						// width: 80%;
					}
				}
				//   display: flex;
				//   width: 100%;
				//   div {
				//     width: 100%;
				//     div {
				//       // font-weight: bold;
				//       // font-size: 1.1rem;
				//     }
				//   }
			}
		}
	}
	.action {
		width: 30%;
		// padding: 20px;
		display: inline-block;
		justify-content: right;
		padding: 0;

		display: table-row;
		color: #f15e2c;
		div {
			font-size: 1.3rem;
			text-align: right;
			padding-bottom: 5%;
		}

		.price__status {
			//   padding-bottom: 10px;
			.status {
				font-size: 1rem;
			}
		}

		.btnAction {
			width: 100%;
			div {
				// padding-top: 10px;
				button {
					width: 60%;
					font-size: 1.2rem;
					height: auto;
				}
			}
			.heartBtn > button {
				color: #fff;
				background-color: #f15e2c;
				// border: 0.5px solid #111;
				// opacity: 0.5;
				// button > :first-child {
				//   background-color: #f15e2c;
				// }
			}
			.deleteBtn > button {
				color: #fff;
				background-color: #f15e2c;
				// border: 0.5px solid #111;
				opacity: 0.9;
			}
		}

		.price__action {
			font-weight: bold;
		}
	}
	button.ant-btn:hover,
	button.ant-btn:focus,
	button.ant-btn:visited,
	button.ant-btn:active,
	button.ant-btn:focus-within {
		// color: #111 !important;
	}
}

@media only screen and (max-width: 768px) {
	.details__cart {
		.img_detail__cart {
			.logo {
				img {
					width: 25vw;
					height: auto;
				}
			}

			.detail {
				.detail__content {
					div {
						font-size: 2.4vw;
					}
				}
				// .size-amount {
				//   div {
				//     font-size: 2.2vw;
				//   }
				// }
			}
		}

		.action {
			font-size: 3vw;
			.price__status {
				.status {
					font-size: 2vw;
				}
			}
		}
	}

	// .btn-delete-back {
